.hero {
  width: 100%;
  z-index: 0;
  position: relative;
}
.hero-text {
  position: absolute;
  top: 160px;
  left: 31px;
  color: #9da4bd;
   z-index: 0; 
  @media screen and (min-width:480px){
   z-index: 0;
  top: 0;
  position: absolute;
  top: 208px;
  left: 31px;
  color: #9da4bd; 
  }
  @media screen and (min-width: 768px) {
    left: 120px;
    top: 272px;
  }
  @media screen and (min-width: 1200px) {
    left: 100px;
    top: 272px;
  }
}
.hero-text > .hero-paragraph::before {
  @media screen and (min-width:480px) {
    position: absolute;
  content: '';
  top: 50%;
  left: 0%;
  width: 60px;
  height: 1px;
  background: rgba(157, 164, 189, 0.6);
  }
  
}
.hero-title {
  font-family: 'Raleway';
  margin-bottom: 20px;
  @media screen and(min-width:480px) {
    margin-bottom: 20px;
  font-weight: bold;
  line-height: 56px;
  letter-spacing: 0.05em;
  font-size: 48px;
  color: white;
  margin-right: 39px;
  }
  @media screen and (min-width: 1200px) {
    font-size: 72px;
    line-height: 85px;
  }
}

  
.hero-paragraph {
  position: relative;
 margin-bottom: 20px;
  @media screen and (min-width:480px) {
   padding-left: 111px; 
  }
  
}
.aside-links {
  @media screen and (max-width: 767px) {
    display: none;
  }
  @media screen and (min-width: 768px) {
    position: absolute;
    flex-direction: column-reverse;
    display: flex;
    width: 100px;
  }
  @media screen and (min-width: 1200px) {
    position: absolute;
    width: 100px;
  }
}
.hero-distribution {
  @media screen and (min-width: 768px) {
    display: flex;
    padding-left: 100px;
  }
}
.list-social-hero {
&::before {
    position: absolute;
    bottom: 40%;
    left: -60px;
    content: '';
    width: 60px;
    height: 1px;
    background: #303030;
  }
  @media screen and (min-width:768px){
position: relative;
  display: flex;
  align-items: center;
  transform: rotate(-90deg);
  top: 499px;
  right: 80px;
  }
  @media screen and (min-width:1200px) {
     position: relative;
     display: flex;
     align-items: center;
     transform: rotate(-90deg);
     top: 499px;
     right: 95px;
    }
    
  }
.social-hero {
  margin-left: 40px;
  font-weight: 600;
  font-size: 11px;
  line-height: 1.364;

  letter-spacing: 0.2em;

  color: var(--white-title-color);
&:hover,
&:focus {
  color: var(--accent-color);
  transition: color 250ms linear;
}
}
.text-hero-desc {
  font-size: 14px;
  line-height: 20px;
  @media screen and (min-width:480px) {
    width: 306px;
  font-weight: normal;
  font-size: 16px;
  line-height: 26px;
  letter-spacing: 0.02em;
  }
  @media screen and (min-width: 768px) {
    width: 355px;
  }
  @media screen and (min-width: 1200px) {
    font-size: 18px;
    line-height: 30px;
    width: 486px;
  }
}
